import React, { useEffect, useState } from "react";
import {
  makeStyles,
  Paper,
  Checkbox,
  Typography,
  IconButton,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
} from "@material-ui/core";
import { Details } from "@material-ui/icons";

import { Traits, Tokens } from "../lib/data";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  main: {
    textAlign: "center",
  },
  title: {
    fontSize: 16,
  },
  body: {
    fontSize: 14,
  },
  selected: {
    background: "#ddeef9",
    "&:hover": {
      background: "#bbddf4",
    },
  },
}));

const TraitSelectCard = ({
  contractAddress,
  setSelectedList,
  selectedList,
  filters,
  addFilter,
  removeFilter,
}) => {
  const classes = useStyles();
  const [traits, setTraits] = useState([]);

  useEffect(() => {
    var poller = setInterval(() => {
      const fetchedTraits = Traits.filter(
        (trait) => trait.contract === contractAddress
      ).reduce((pV, cV) => {
        let trait = {};
        if (pV.find((trait) => trait.trait_type === cV.trait_type)) {
          trait = { ...pV.find((trait) => trait.trait_type == cV.trait_type) };
          trait.variants.push(cV.value);
          trait.trait_count += cV.trait_count;
          return pV.map((tt) => {
            if (tt.trait_type !== cV.trait_type) return tt;
            return trait;
          });
        } else {
          trait.trait_type = cV.trait_type;
          trait.variants = [cV.value];
          trait.trait_count = cV.trait_count;
          return [...pV, trait];
        }
      }, []);

      setTraits(fetchedTraits);
    }, 300);

    return () => clearInterval(poller);
  }, []);

  return (
    <Paper>
      <List>
        {traits.map((trait) => {

          const checkedIndex = filters.findIndex(
            (filter) => {
              return filter.variant === "trait_exist" &&
                filter.payload?.includes(trait.trait_type)
            }
          )

          return (
            <ListItem
              className={selectedList == trait.trait_type ? classes.selected : ""}
              onClick={() => setSelectedList(trait.trait_type)}
              button
              key={trait.trait_type}
            >
              <ListItemText
                primary={`${trait.trait_type}  - ${trait.variants.length}  variant`}
                secondary={`${trait.trait_count}  have this trait (${parseFloat(
                  (trait.trait_count * 100) / Tokens.length
                ).toFixed(2)}%) `}
              />

              <ListItemSecondaryAction>
                <Checkbox
                  checked={checkedIndex > -1}
                  onChange={() => {
                    if (checkedIndex > -1) {
                      removeFilter(checkedIndex)
                    } else {
                      addFilter({ variant: "trait_exist", payload: [trait.trait_type] })
                    }
                  }}
                />
              </ListItemSecondaryAction>
            </ListItem>
          )
        })}
      </List>
    </Paper>
  );
};

export default TraitSelectCard;
