import React, { useEffect, useState } from "react";
import { makeStyles, Typography } from "@material-ui/core";
import { Tokens, Traits } from "../lib/data";

const useStyles = makeStyles((theme) => ({
    root: {},
    title: {
        textAlign: "center",
        color: theme.palette.primary.main,
    },
}));

const SearchState = ({ contractAddress }) => {
    const classes = useStyles();

    const [traitsCounts, setTC] = useState(0);
    const [tokenCounts, setTO] = useState(0);
    const [timerInterval, setTI] = useState(0);



    useEffect(() => {
        let traits = Traits.filter((trait) => trait.contract === contractAddress);
        setTC(traits.length);
        setTO(Tokens.filter((token) => token.contract === contractAddress).length);
    }, [timerInterval]);

    useEffect(() => {
        let i = 0
        var poller = setInterval(() => {
            setTI(i++);
        }, 1000);

        return function cleanup() {
            clearInterval(poller);
        };
    }, []);

    return (
        <div className={classes.root}>
            <Typography className={classes.title} variant="h6">
                Successfully fetched Metadata of {tokenCounts}  tokens with {traitsCounts} unique trait
            </Typography>
        </div>
    );
};

export default SearchState;
