import React from "react";
import {
  makeStyles,
  Paper,
  Chip,
  Typography,
  Checkbox,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
  chip: {
    marginRight: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  title: {
    marginBottom: theme.spacing(2),
  },
  container: {
    display: "flex",
    justifyContent: "space-between",
  },
  controllers: {
      display: 'flex',
      alignItems: 'center'
  },
  filterLabel: {
      marginRight: theme.spacing(1)
  }
}));

const FilterManager = ({ filters, addFilter, removeFilter }) => {
  const classes = useStyles();

  const onlyForSaleIndex = filters.findIndex((filter) => {
    return filter.variant === "only_sales";
  });

  return (
    <Paper className={classes.root}>
      <Typography className={classes.title} variant="h5">
        Filters
      </Typography>
      <div className={classes.container}>
        <div>
          {filters
            .filter(
              (fl) =>
                fl.variant === "trait_exist" || fl.variant === "specific_trait"
            )
            .map((filter, index) => (
              <Chip
                color="secondary"
                size="small"
                onDelete={() => removeFilter(index)}
                className={classes.chip}
                label={filter.payload.join("-")}
              />
            ))}
        </div>
        <div className={classes.controllers}>
          <Typography className={classes.filterLabel} variant="body2">For Sales Only</Typography>
          <Checkbox
            checked={onlyForSaleIndex > -1}
            onChange={() => {
              if (onlyForSaleIndex > -1) {
                removeFilter(onlyForSaleIndex);
              } else {
                addFilter({ variant: "only_sales" });
              }
            }}
          />
        </div>
      </div>
    </Paper>
  );
};

export default FilterManager;
