import React, { useState } from "react";
import {
  makeStyles,
  Paper,
  Typography,
  IconButton,
  Divider,
  Chip,
  Dialog,
  DialogTitle,
  Table,
  TableBody,
  TableHead,
  TableRow,
  TableCell,
} from "@material-ui/core";
import { Equalizer } from "@material-ui/icons";
import { Row, Col, Container } from "react-grid-system";
import { Tokens } from "../lib/data";

import { ReactComponent as OpenSea } from "./opensea.svg";
import { ReactComponent as Ethereum } from "./ethereum.svg";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  header: {
    display: "flex",
    justifyContent: "space-between",
    marginBottom: theme.spacing(1),
  },
  id: {
    color: theme.palette.primary.dark,
  },
  main: {
    marginTop: theme.spacing(1),
  },
  rarityInfo: {
    display: "flex",
    justifyContent: "space-between",
  },
  rarityItem: {
    padding: theme.spacing(1),
    textAlign: "center",
  },
  rarityValue: {
    color: theme.palette.secondary.dark,
    fontWeight: "600",
  },
  rarityTitle: {
    color: "#333",
  },
  tokenImage: {
    width: "100%",
    borderRadius: "8px 8px 0 0",
    marginTop: theme.spacing(2),
  },
  collectionName: {
    background: theme.palette.primary.dark,
    padding: theme.spacing(0.5),
    borderRadius: `0 0 8px 8px`,
    textAlign: "center",
    marginTop: -4,
    fontWeight: "bold",
    color: "#f8f8f8",
  },
  footer: {
    display: "flex",
    justifyContent: "space-between",
    marginTop: theme.spacing(2),
  },
  footerItem: {
    textAlign: "center",
  },
  footerTitle: {
    color: "#999",
  },
  openSea: {
    width: 100,
    height: 50,
  },
  eth: {
    width: 20,
    height: 20,
    marginLeft: 8,
  },
  footerValue: {
    display: "flex",
    alignItems: "center",
  },
  dialogRoot: {
    maxWidth: "none !important",
  },
  container: {
    padding: theme.spacing(2),
  },
  infoCol: {
    minWidth: 300,
  },
  metaCol: {
    minWidth: 600,
  },
}));

const NFTDialog = ({ open, handleClose, token }) => {
  const classes = useStyles();

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="simple-dialog-title"
      open={open}
      className={classes.dialogRoot}
      maxWidth="lg"
    >
      <DialogTitle id="simple-dialog-title"> ID: {token.tokenId}</DialogTitle>
      <Divider />
      <Container className={classes.container}>
        <Row>
          <Col md={4} className={classes.infoCol}>
            <div className={classes.main}>
              <div className={classes.rarityInfo}>
                <div className={classes.rarityItem}>
                  <Typography className={classes.rarityTitle} variant="body1">
                    Rarity Rank
                  </Typography>
                  <Typography className={classes.rarityValue} variant="body2">
                    {token.rank}/{Tokens.length}
                  </Typography>
                </div>
                <div className={classes.rarityItem}>
                  <Typography className={classes.rarityTitle} variant="body1">
                    Rarity Score
                  </Typography>
                  <Typography className={classes.rarityValue} variant="body2">
                    {token.rarityScore.toFixed(2)}
                  </Typography>
                </div>
              </div>
              <div className={classes.imageInfo}>
                <img className={classes.tokenImage} src={token.image} />
                <Typography variant="h6" className={classes.collectionName}>
                  {token.collectionName}
                </Typography>
              </div>
            </div>

            <div className={classes.footer}>
              <div className={classes.footerItem}>
                <Typography className={classes.footerTitle} variant="body1">
                  Status
                </Typography>
                <Typography className={classes.footerValue} variant="body2">
                  {token.isForSale ? (
                    <Chip color="primary" size="small" label="For Sale" />
                  ) : (
                    <Chip size="small" color="secondary" label="Not For Sale" />
                  )}
                </Typography>
              </div>
              <div
                style={{ cursor: "pointer" }}
                onClick={() =>
                  window.open(
                    `https://opensea.io/assets/${token.contract}/${token.tokenId}`,
                    "_blank"
                  )
                }
                className={classes.footerItem}
              >
                <OpenSea className={classes.openSea} />
              </div>
              <div className={classes.footerItem}>
                <Typography className={classes.footerTitle} variant="body1">
                  Price
                </Typography>
                <Typography className={classes.footerValue} variant="body2">
                  {token.price} <Ethereum className={classes.eth} />
                </Typography>
              </div>
            </div>
          </Col>
          <Col className={classes.metaCol} md={8}>
            <Table className={classes.table} aria-label="a dense table">
              <TableHead>
                <TableRow>
                  <TableCell align="center">Trait</TableCell>
                  <TableCell align="center">Value</TableCell>
                  <TableCell align="center">Amount</TableCell>
                  <TableCell align="center">%</TableCell>
                  <TableCell align="center">Score</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {token.tradTraits.map((trait) => (
                  <TableRow key={trait.trait_type}>
                    <TableCell align="center">{trait.trait_type}</TableCell>
                    <TableCell align="center">{trait.value}</TableCell>
                    <TableCell align="center">{trait.trait_count}</TableCell>
                    <TableCell align="center">
                      {((trait.trait_count * 100) / Tokens.length).toFixed(
                        2
                      )}
                    </TableCell>
                    <TableCell align="center">
                      {(Tokens.length / trait.trait_count).toFixed(2)}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Col>
        </Row>
      </Container>
    </Dialog>
  );
};

const NFTCard = ({ token }) => {
  const classes = useStyles();
  const [dialogOpen, setDO] = useState(false);

  return (
    <>
      <Paper className={classes.root}>
        <div className={classes.header}>
          <Typography className={classes.id} variant="h6">
            ID: {token.tokenId}
          </Typography>
          <IconButton onClick={() => setDO(true)} color="primary">
            <Equalizer />
          </IconButton>
        </div>
        <Divider />
        <div className={classes.main}>
          <div className={classes.rarityInfo}>
            <div className={classes.rarityItem}>
              <Typography className={classes.rarityTitle} variant="body1">
                Rarity Rank
              </Typography>
              <Typography className={classes.rarityValue} variant="body2">
                {token.rank}/{Tokens.length}
              </Typography>
            </div>
            <div className={classes.rarityItem}>
              <Typography className={classes.rarityTitle} variant="body1">
                Rarity Score
              </Typography>
              <Typography className={classes.rarityValue} variant="body2">
                {token.rarityScore.toFixed(2)}
              </Typography>
            </div>
          </div>
          <div className={classes.imageInfo}>
            <img className={classes.tokenImage} src={token.image} />
            <div className={classes.collectionName}>{token.collectionName}</div>
          </div>
        </div>

        <div className={classes.footer}>
          <div className={classes.footerItem}>
            <Typography className={classes.footerTitle} variant="body1">
              Status
            </Typography>
            <Typography className={classes.footerValue} variant="body2">
              {token.isForSale ? (
                <Chip color="primary" size="small" label="For Sale" />
              ) : (
                <Chip size="small" color="secondary" label="Not For Sale" />
              )}
            </Typography>
          </div>
          <div
            style={{ cursor: "pointer" }}
            onClick={() =>
              window.open(
                `https://opensea.io/assets/${token.contract}/${token.tokenId}`,
                "_blank"
              )
            }
            className={classes.footerItem}
          >
            <OpenSea className={classes.openSea} />
          </div>
          <div className={classes.footerItem}>
            <Typography className={classes.footerTitle} variant="body1">
              Price
            </Typography>
            <Typography className={classes.footerValue} variant="body2">
              {token.price} <Ethereum className={classes.eth} />
            </Typography>
          </div>
        </div>
      </Paper>
      <NFTDialog
        open={dialogOpen}
        handleClose={() => setDO(false)}
        token={token}
      />
    </>
  );
};

export default NFTCard;
