import { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core";
import { Row, Col } from "react-grid-system";
import Pagination from "material-ui-flat-pagination";

import NFTCard from "./NFTCard";

import { Tokens } from "../lib/data";

const useStyles = makeStyles((theme) => ({
  root: {},
  pagination: {
    marginTop: theme.spacing(2),
    display: "flex",
    justifyContent: "center",
  },
}));

const RareTokens = ({ filters }) => {
  const classes = useStyles();

  const [tokens, setTokens] = useState([]);
  const [timerInterval, setTI] = useState(0);
  const [limit, setLimit] = useState(20);
  const [offset, setOffset] = useState(0);

  useEffect(() => {
    let tks = Tokens.sort((a, b) => {
      return -1 * (parseFloat(a.rarityScore) - parseFloat(b.rarityScore));
    })
      .map((token, i) => ({ ...token, rank: i + 1 }))
      .filter((token) => {
        if (!filters.length) return true;
        var isValid = false;
        filters.forEach((fil) => {
          switch (fil.variant) {
            case "only_sales":
              isValid = token.isForSale;
              break;
            case "trait_exist":
              isValid = !!token.traits[fil.payload[0]];
              break;
            case "specific_trait":
              isValid = token.traits[fil.payload[0]] == fil.payload[1];
              break;
            default:
              break;
          }
        });
        return isValid;
      })
      .slice(offset, offset + limit);
    setTokens(tks);
  }, [timerInterval]);

  useEffect(() => {
    let i = 0;
    var poller = setInterval(() => {
      setTI(i++);
    }, 2.5 * 1000);

    return function cleanup() {
      clearInterval(poller);
    };
  }, []);

  return (
    <div className={classes.root}>
      <Row>
        {tokens.map((token) => (
          <Col key={token.id} md={3}>
            <NFTCard token={token} />
          </Col>
        ))}
      </Row>
      <div className={classes.pagination}>
        <Pagination
          limit={limit}
          offset={offset}
          total={Tokens.length}
          onClick={(e, offset) => setOffset(offset)}
        />
      </div>
    </div>
  );
};

export default RareTokens;
