const tokenMapper = (asset) => {
  return {
    token: {
      id: asset.id,
      tokenId: asset.token_id,
      image: asset.image_url,
      contract: asset.asset_contract.address,
      collectionName: asset.collection.name,
      isForSale: !!asset.sell_orders?.length,
      traits: asset.traits.reduce((pV, cV) => {
        let retObj = {
          ...pV,
        };
        retObj[cV.trait_type] = cV.value;
        return retObj;
      }, {}),
      rarityScore: asset.traits.reduce((pV, cV) => {
        return pV + Tokens.length / cV.trait_count;
      }, 0),
      price: asset.sell_orders
        ? asset.sell_orders[0].current_price / 10 ** 18
        : 0,
      owner: asset.owner.address,
      tradTraits: asset.traits,
    },
    traits: [
      ...asset.traits.map((trait) => ({
        ...trait,
        contract: asset.asset_contract.address,
      })),
    ],
  };
};

