const useStyles = makeStyles((theme) => ({
  inputRoot: {
    height: `calc(100vh - 56px)`,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  inputContainer: {
    textAlign: "center",
  },
  contractInput: {
    marginBottom: theme.spacing(2),
  },
}));

function App() {
  const classes = useStyles();

  const [filters, setFilters] = useState([]);
  const [contractInput, setContractInput] = useState("");
  const [selectedContract, setSelectedContract] = useState(null);
  const [timerInterval, setTI] = useState(0);


  useEffect(() => {
    let i = 0;
    const options = { method: "GET" };

    var poller = setInterval(() => {
      fetch(
        `https://api.opensea.io/api/v1/assets?asset_contract_address=${selectedContract}&order_direction=desc&offset=${
          i * 50
        }&limit=50`,
        options
      )
        .then((response) => response.json())
        .then((response) => {
          if (!response.assets || !response.assets.length) {
            clearInterval(poller);
          }
          response.assets.map(tokenMapper).forEach(({ token, traits }) => {
            addToken(token);
            traits.forEach((trait) => addTrait(trait));
          });
        })
        .catch((err) => {
          clearInterval(poller);

          console.error(err);
        });

      i++;
    }, 1000);

    return function cleanup() {
      clearInterval(poller);
    };
  }, [selectedContract]);

  const addFilter = (filter) => {
    setFilters([...filters, filter]);
  };

  const removeFilter = (index) => {
    setFilters(filters.filter((item, i) => i !== index));
  };

  const ContractInput = () => {
    return (
      <div className={classes.inputRoot}>
        <div className={classes.inputContainer}>
          <TextField
            fullWidth
            variant="filled"
            label="Contract Address"
            onChange={(e) => setContractInput(e.target.value)}
            value={contractInput}
            className={classes.contractInput}
          />
          <Button
            onClick={() => setSelectedContract(contractInput)}
            variant="contained"
            color="primary"
          >
            Set Contract
          </Button>
        </div>
      </div>
    );
  };

  const MainApp = () => {
    return (
      <>
        {" "}
        <div style={{ padding: 16 }}>
          <SearchState contractAddress={selectedContract} />
        </div>
        <div style={{ padding: 16 }}>
          <TraitsList
            addFilter={addFilter}
            removeFilter={removeFilter}
            filters={filters}
            contractAddress={selectedContract}
          />
        </div>
        <div style={{ padding: 16 }}>
          <FilterManager
            addFilter={addFilter}
            removeFilter={removeFilter}
            filters={filters}
          />
        </div>
        <div style={{ padding: 16 }}>
          <RareTokens filters={filters} contractAddress={selectedContract} />
        </div>{" "}
      </>
    );
  };

  console.log(selectedContract);

  return (
    <ThemeProvider theme={theme}>
      <div>
        <Header />
        {selectedContract ? MainApp() : ContractInput()}
      </div>
    </ThemeProvider>
  );
}

export default App;
