let Tokens = []
let Traits = []


const addToken = (token) => {
    if (!Tokens.find(tk => (tk.tokenId == token.tokenId && tk.contract == token.contract))) {
        Tokens.push(token)
    }
}

const addTrait = (trait) => {
    if (!Traits.find(tt => (
        tt.contract === trait.contract &&
        tt.trait_type === trait.trait_type &&
        tt.value === trait.value
    ))) {
        Traits.push({...trait})
    }
}

export {
    Tokens,
    Traits,
    addToken,
    addTrait,
}