import React, { useEffect, useState } from "react";
import { makeStyles, Checkbox } from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

import { Traits, Tokens } from "../lib/data";

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

const TraitTable = ({
  contractAddress,
  traitName,
  filters,
  addFilter,
  removeFilter,
}) => {
  const classes = useStyles();

  const [traits, setTraits] = useState([]);
  const [total, setTotal] = useState(1);
  const [timerInterval, setTI] = useState(0);

  useEffect(() => {
    let currentTraits = Traits.filter(
      (trait) =>
        trait.contract === contractAddress && trait.trait_type === traitName
    );
    setTraits(currentTraits);
    setTotal(Tokens.length);
  }, [traitName, timerInterval]);

  useEffect(() => {
    let i = 0;
    var poller = setInterval(() => {
      setTI(i++);
    }, 5000);

    return function cleanup() {
      clearInterval(poller);
    };
  }, []);

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell></TableCell>
            <TableCell align="center">Value</TableCell>
            <TableCell align="center">Amount</TableCell>
            <TableCell align="center">Percentage</TableCell>
            <TableCell align="center">Score</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {traits.map((trait) => {
            const checkedIndex = filters.findIndex(
              (filter) => {
                return filter.variant === "specific_trait" &&
                  filter.payload?.includes(trait.trait_type) &&
                  filter.payload?.includes(trait.value)
              }
            )

            return (
              <TableRow key={trait.value}>
                <TableCell align="center">
                  <Checkbox
                    checked={checkedIndex > -1}
                    onChange={() => {
                      if (checkedIndex > -1) {
                        removeFilter(checkedIndex)
                      } else {
                        addFilter({ variant: "specific_trait", payload: [trait.trait_type, trait.value] })
                      }
                    }}
                  />
                </TableCell>

                <TableCell align="center">{trait.value}</TableCell>
                <TableCell align="center">{trait.trait_count}</TableCell>
                <TableCell align="center">
                  {((trait.trait_count * 100) / Tokens.length).toFixed(2)}
                </TableCell>
                <TableCell align="center">
                  {(Tokens.length / trait.trait_count).toFixed(2)}
                </TableCell>
              </TableRow>
            )
          })}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TraitTable;
